html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  min-width: 620px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  user-select: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header__wrapper {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  z-index: 2;
  padding-top: 21px;
  padding-bottom: 21px;
}
.main-header__wrapper::after {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.3);
}

