.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 5px;
  min-height: 55px;
  border: 3px solid #ffffff;
  border-radius: 30px;
  line-height: 1;
  font-size: 18px;
  color: #ffffff;
  font-weight: 700;
  cursor: pointer;
}

.toggles {
  box-sizing: content-box;
  display: flex;
  justify-content: center;
}

.toggles__item {
  border: 0;
  padding: 0;
  background: none;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: rgba(40, 54, 69, 0.3);
  border: 3px solid #ffffff;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
  cursor: pointer;
}
.toggles__item:hover {
  background-color: rgba(40, 54, 69, 0.75);
}

.toggles__item--active {
  background-color: #ffffff;
  border-color: #283645;
}
.toggles__item--active:hover {
  background-color: #ffffff;
}

